<template>
  <div class="setting">
    <div class="login" v-if="!token">
      <h3>立即注册</h3>
      <button @click="getLogRegState(0)">注册</button>
    </div>
    <div class="register" v-if="!token">
      <h3>立即登录</h3>
      <button @click="getLogRegState(1)">登录</button>
    </div>
    <div class="username" v-if="token">
      <h3>修改用户名</h3>
    </div>
    <div class="password" v-if="token">
      <h3>修改密码</h3>
    </div>
    <div class="register" v-if="token">
      <h3>退出登录</h3>
      <button @click="LogOut">退出</button>
    </div>
  </div>
</template>

<script>
import {mapState} from "vuex"
export default {
  name: "MySetting",
  inject:['reload'],
  data() {
    return {};
  },
  computed: {

    ...mapState(['changekey']),
    token() {
      return localStorage.getItem("TOKEN");
    },
  },
  mounted() {},

  methods: {
    // 获取type并打开login遮罩层
    getLogRegState(type) {
      this.$store.dispatch("changelogMaskShow", true);
      this.$bus.$emit("changeLogRegMask", type);
    },
    // 退出登录
    LogOut() {
      localStorage.removeItem("TOKEN");
        this.reload()
        this.$bus.$emit("alertMsg", "退出登录成功");
    },
  },
};
</script>

<style lang="less" scoped>
.setting {
  padding: 40px 100px;

  & > div {
    padding: 10px;
    height: 60px;
    background-color: #fff;
    margin-bottom: 30px;
    border-radius: 10px;
  }

  .login,
  .register {
    padding: 10px 50px;

    h3 {
      float: left;
      font-size: 18px;
      line-height: 60px;
      color: rgb(51, 118, 247);
    }

    button {
      float: left;
      border: none;
      outline: none;
      margin-top: 10px;
      margin-left: 70%;
      width: 150px;
      height: 40px;
      border-radius: 30px;
      color: #fff;
      background-color: rgb(51, 118, 247);
    }
  }

  .username,
  .password {
    padding: 10px 50px;

    h3 {
      float: left;
      font-size: 18px;
      line-height: 60px;
      color: rgb(51, 118, 247);
    }
  }
}
</style>